.cost {
	padding: 35px 25px;
	@include f(16px,500,1.875);
	background-color: $white;
	box-shadow: 0 20px 40px -15px #E3E6F5;
	border-radius: 8px;
	@include sm {
		padding: 25px 15px; }
	&__field {
		align-items: center;
		display: flex;
		@include sm {
			flex-wrap: wrap; }
		&:not(:first-child) {
			margin-top: 30px;
			@include sm {
				margin-top: 14px;
				padding-top: 22px;
				border-top: 1px solid #EBEDF8; } } }
	&__text {
		@include fns;
		@include f(16px,900,1.5625);
		letter-spacing: 0.025em;
		@include fs(52%);
		padding-right: 10px;
		@include sm {
			margin-bottom: 5px;
			padding-right: 0;
			@include fs(100%);
			font-size: 14px; } }
	&__time {
		padding-right: 10px;
		@include fs(20%);
		text-align: center;
		color: $textColor;
		@include sm {
			@include fs(50%);
			order: 3;
			font-size: 14px;
			text-align: right; } }
	&__tot {
		@include fs(28%);
		font-weight: 600;
		text-align: right;
		color: $blue;
		@include sm {
			@include fs(50%);
			text-align: left; } } }
