@include generate_grid();

.inner {
	margin: 0 auto;
	padding: 0 50px;
	max-width: 1440px;
	@include lg {
		padding: 0 30px; }
	@include sm {
		padding: 0 14px; } }

.wrap {
	padding: 70px 0;
	margin: 0 -18px;
	display: flex;
	@include md {
		padding: 30px 0 60px; }
	@include sm {
		padding: 20px 0 60px;
		display: block; }
	&_pn {
		padding: 0; } }

.col-left {
	padding: 0 16px;
	@include fs(316px);
	flex-shrink: 0;
	@include lg {
		@include fs(260px); }
	@include r(1180) {
		display: none; }
	.form & {
		@include r(1180) {
			display: none; } } }
.col-main {
	padding: 0 16px;
	flex-grow: 1;
	overflow: hidden;
	@include sm {
		overflow: visible; }
	.form & {
		overflow: visible; } }
.col-right {
	padding: 0 16px;
	@include fs(332px);
	flex-shrink: 0;
	@include lg {
		@include fs(260px); }
	@include sm {
		padding-top: 30px;
		@include fs(100%); }
	.form & {
		@include r(1180) {
			display: none; } } }

.sidebar {
	&__title {
		&:not(:last-child) {
			margin-bottom: 16px; } }
	&__group {
		&:not(:first-child) {
			margin-top: 39px; } }
	&__field {
		&:not(:first-child) {
			margin-top: 15px; } } }

.section {
	&:not(:last-child) {
		margin-bottom: 40px;
		@include sm {
			margin-bottom: 35px; } }
	p {
		color: $textColor;
		&:not(:last-child) {
			margin-bottom: 12px; } }
	&_white {
		padding: 28px 25px;
		border-radius: 8px;
		box-shadow: 0 20px 40px -15px #E3E6F5;
		background-color: $white;
		@include sm {
			padding: 28px 15px; } }
	&_blue {
		padding: 28px 25px;
		color: $white;
		border-radius: 8px;
		box-shadow: 0 40px 44px -30px #6A81DF;
		background-color: $blue;
		@include sm {
			padding: 28px 15px; }
		p {
			color: $white; } }
	&__title {
		margin-bottom: 18px;
		@include sm {
			margin-bottom: 9px; }
		&:not(:first-child) {
			margin-top: 36px;
			@include sm {
				margin-top: 28px; } }
		&_md50 {
			margin-bottom: 50px;
			@include sm {
				margin-bottom: 40px; } } }
	&__title-sm {
		margin-bottom: 16px;
		&_bord {
			position: relative;
			padding-bottom: 25px;
			&:before {
				position: absolute;
				content: '';
				bottom: 0;
				left: -25px;
				right: -25px;
				height: 1px;
				background-color: currentColor; } }
		&:not(:first-child) {
			margin-top: 38px; }
		& + .phone {
			margin-top: -15px; } }
	.img {
		margin: 0 -25px;
		&:not(:first-child) {
			margin-top: 30px; }
		&:not(:last-child) {
			margin-bottom: 30px; }
		& + .section__title {
			margin-top: 30px; } } }

.img img {
	max-width: 100%;
	display: block; }

.map {
	height: 317px;
	&:not(:first-child) {
		margin-top: 15px; }
	&:not(:last-child) {
		margin-bottom: 15px; } }

.text-sm,
.text-sm p {
	@include f(12px,500,2); }
.text-md {
	@include f(14px,500,1.93); }

.requisite-btn-wrap {
	text-align: right;
	@include sm {
		margin-top: 20px;
		text-align: left; }
	.btn {
		@include xs {
			width: 100%; } } }
.phone {
	@include fns;
	@include f(18px,700,1.578);
	letter-spacing: 0.04em; }

.md-hide {
	@include md {
		display: none !important; } }
.md-show {
	display: none;
	@include md {
		display: block; } }

.sm-hide {
	@include sm {
		display: none !important; } }
.sm-show {
	display: none;
	@include sm {
		display: block; } }
