html {
	&.is-open-menu {
		overflow: scroll; } }

body {
	@include fmt;
	@include f(14px,400,1.7);
	min-width: 320px;
	letter-spacing: 0.0113em;
	color: $mainColor;
	@include font-smoothing;
	background: $bodyBg;
	@include sm {
		line-height: 2.2; }
	.is-open-menu & {
		position: fixed;
		height: 100vh;
		width: 100%;
		overflow: hidden; } }


.out {
	position: relative;
	overflow: hidden; }

.title-lg {
	@include fnssc;
	@include f(54px,900,1.2);
	letter-spacing: 0.015em;
	@include md {
		font-size: 34px; } }
.title {
	@include fnssc;
	@include f(36px,900,1.3);
	@include md {
		font-size: 28px; }
	@include sm {
		font-size: 22px;
		line-height: 1.8; }
	&_mob-lg {
		@include sm {
			font-size: 25px;
			line-height: 1.3; } } }
.title-md {
	@include fnssc;
	@include f(30px,900,1.44);
	@include md {
		font-size: 19px; } }
.title-sm {
	@include fnssc;
	@include f(22px,900,1.44);
	letter-spacing: 0.01em;
	@include md {
		font-size: 20px; }
	@include sm {
		font-size: 19px; } }

.info-list {
	@include fmt;
	letter-spacing: -0.01em;
	align-items: center;
	flex-wrap: wrap;
	display: flex;
	&__item {
		position: relative;
		&:not(:last-child) {
			padding-right: 10px;
			&:before {
				position: absolute;
				content: '';
				top: 50%;
				margin-top: -1px;
				right: 4px;
				@include circle(2);
				background-color: #9799A5; } } } }

.text-blue {
	color: $blue; }

.tag,
.data {
	text-transform: uppercase;
	color: #9799A5;
	@include f(12px,500,1.25);
	color: #9799A5; }

.links {
	.link {
		&:not(:first-child) {
			margin-top: 15px; } } }
.link {
	position: relative;
	padding: 19px 30px 19px 15px;
	@include fnssc;
	@include f(16px,900,1.428);
	letter-spacing: 0.03em;
	color: $mainColor;
	background-color: $white;
	box-shadow: 0 10px 30px -5px #E3E6F5;
	border-radius: 6px;
	transition: box-shadow .3s;
	display: block;
	@include hover {
		box-shadow: 0 8px 20px 0 darken(#E3E6F5, 5%); }
	.icon {
		position: absolute;
		top: 50%;
		right: 15px;
		margin-top: -6px;
		line-height: 1;
		color: $blue;
		font-size: 12px; } }

.simple-link {
	@include f(14px,400,1.285);
	color: $textColor;
	transition: color .3s;
	@include hover {
		color: $mainColor; } }

.text-blue {
	color: $blue; }
.text-uppercase {
	text-transform: uppercase; }

.list {
	@include fns;
	@include f(14px,700,1.875);
	&:not(:first-child) {
		margin-top: 18px; }
	&:not(:last-child) {
		margin-bottom: 28px; }
	li {
		position: relative;
		padding-left: 40px;
		@include sm {
			padding-left: 10px; }
		&:before {
			position: absolute;
			top: 12px;
			left: 0;
			content: '';
			width: 20px;
			height: 2px;
			border-radius: 2px;
			background-color: $blue;
			@include sm {
				width: 4px;
				height: 2px; } }
		&:not(:last-child) {
			margin-bottom: 19px;
			@include sm {
				margin-bottom: 14px; } } } }

.img-list {
	margin-bottom: -18px;
	flex-wrap: wrap;
	display: flex;
	&__item {
		position: relative;
		margin-bottom: 18px;
		padding: 0 10px 0 20px;
		@include fns;
		@include f(14px,700,1.785);
		@include fs(50%);
		@include sm {
			@include fs(100%); }
		&:before {
			position: absolute;
			content: '';
			top: 7px;
			left: 0;
			width: 10px;
			height: 10px;
			opacity: 1;
			background-image: url('../img/circle-sm.svg');
			background-repeat: no-repeat;
			background-size: contain;
			background-position: center; } } }
